// importwebpack
import { useTable } from './hooks/useTable'; const requireComponents = require.context('./components', true, /\.vue$/)

const install = (Vue) => {
  if (install.installed) return
  // 
  requireComponents.keys().forEach(fileName => {
    const config = requireComponents(fileName) // 
    const componentName = config.default.name // vuename
    Vue.component(componentName, config.default) // 
  })
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install
}

export {
  useTable
}
